<template>
  <div>
     <van-nav-bar title="商品" left-arrow @click-right="$router.go(-1)" />
      <div v-if="goodsList!=null">
          <van-card
  :price="item.price"
  :title="item.goodsname"
  :thumb="$preImg+item.img"
  v-for="item in goodsList" :key="item.id"
/>
      </div>
<van-empty v-else
  class="custom-image"
  image="https://img.yzcdn.cn/vant/custom-empty-image.png"
  description="暂无数据"
/>
  </div>
</template>

<script>
import {goods} from "../util/request"
export default {
    data(){
        return {
            goodsList:[]
        }
    },
    mounted(){
        goods(this.$route.query).then(data=>{
            this.goodsList=data.list
        })
        
    }
}
</script>

<style>

</style>


